import React, { Component } from 'react'
import { ListGroup } from 'reactstrap'
import { connect } from 'react-redux'

import ListItem from '../../containers/BlogFather/ListItem';
import { fetchBlog } from '../../redux/actions/blog';
import CustomSpinner from '../../containers/utils/Spinner';

class List extends Component {
    componentDidMount(){
        this.props.fetchBlog(true)
    }

    componentDidUpdate(prevProps){
        if (!Array.isArray(prevProps.stories.items)) 
            this.props.fetchBlog(true)
    }

    render() {
        if (this.props.hasError) {
            return <p> OOoooops... We have some troubles</p>
        }

        if (this.props.isLoading ) {
            return <CustomSpinner />
        }

        const {stories} = this.props
        
        return (
            <ListGroup className="mb-3">
                {stories.items && Array.isArray(stories.items) && stories.items.map((val, i) => <ListItem key={i} id={parseInt(val.id)} name={val.name} likes={val.likes}/>)}
            </ListGroup>
        )
    }
}

List.defaultProps = {
    stories: []
}

const mapStateToProps = (state, ownProps) => ({
    isLoading: state.blog.isLoading,
    hasError: state.blog.hasError,
    stories: state.blog.data
})

const mapDispatchToProps = (dispatch) => ({
    fetchBlog: (short, id) => dispatch(fetchBlog(short, id))
})


export default connect(mapStateToProps, mapDispatchToProps)(List)   