import React, { Component } from 'react'
import {Container} from 'reactstrap'
import List from './List';

export default class BlogFather extends Component {
    render() {
        return (
            <Container className="mt-5">
                <h4 className="text-center" style={{ color: '#4E706F'}}>В своём блоге я обычно делюсь смешными моментами с уроков.
                    Предлагаю с некоторымы из них познакомиться, выбирайте.</h4>
                <Container>
                    <List/>
                </Container>
            </Container>
        )
    }
}
